.ds-ribbon {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  min-width: 750px;
  height: 40px;
  cursor: pointer;

  .ds-ribbon-container {
    display: flex;
    flex: 1;
    flex-direction: row;
    align-items: center;

    padding: 0 80px;
    justify-content: space-between;

    .ds-ribbon-content {
      display: flex;
      flex-direction: row;
      font-style: normal;
      font-weight: bold;
      font-size: 18px;
      line-height: 24px;

      svg {
        margin-right: 24px;
      }
    }
  }
}

.ds-ribbon.default {
  background-color: #713bdb;
  color: white;
}
